let moduleNumber
let moduleTitle

export function createVideoIntroPage(json){
  const content = `<!doctype html>
<html>
  <head>
    <meta charset="utf-8">
    <title>Video Intro</title>
    <script src="interface/js/jquery.js"></script>
    <script src="assets/js/pages.js"></script>
    <script src="interface/js/video_intro.js" defer></script>
    <link rel="stylesheet" href="https://vjs.zencdn.net/7.4.1/video-js.css">
    <link rel="stylesheet" href="interface/css/style-variables.css">
    <link rel="stylesheet" type="text/css" href="interface/css/style.css">
    <link rel="stylesheet" href="interface/css/video_intro.css">
  </head>
  <body class="redBG">
    <div class="videoFullWidthBar"></div>
    <div class="videoContainer intro" style="display:none">
      <video class="video-js" controls preload="auto" width="1024" height="768" data-setup="{}"></video>
    </div>
    <script src="https://vjs.zencdn.net/7.4.1/video.js"></script>
    <script src="interface/js/media_control.js"></script>
  </body>
</html>`
  const file = new File([content], json.screenNumber, { type: 'text/html'} )
  return file
}

export function setModuleNumber(json){
  //ERROR HANDLE IF NOT NUMBER
  const r = /\d+/
  moduleNumber = json.screenContent.match(r)[0]
}

export function setModuleTitle(json){
  const start = json.screenContent.indexOf(':')+1
  const end = json.screenContent.indexOf('</p>')
  moduleTitle = json.screenContent.substring(start, end).trim()
}

export function getModuleNumber(){
  return moduleNumber
}

export function getModuleTitle(){
  return moduleTitle
}